.texto{
  font-size: 20px;
  padding: 10px;
  border-radius: 5px;
  background-color: rgb(0, 0, 0, 0.7) ;
  text-decoration: none;
  margin: 10px;
  color: #ffffff;
  cursor: pointer;
}

.desc{
  color: #0c0b0c;
  font-size: 18px;

}

/* header */
.header-container{
  display: flex;
  width: 100%;
  height: 400px;
  /* border: 1px solid red; */
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: url('./img/bookcase-1869616_1280.jpg');

}

.form-input{
  width: 300px;
  padding: 10px;
  border-radius: 5px;
  border: none;
}

.button{
  width: 100px;
  height: 46px;
  background-color: rgba(216, 180, 19);
  color: #ffffff;
  border-radius: 5px;
  border: none;
  opacity: 0.9;
  transition: opacity 0.5s;
 
}
.button:hover{
  opacity: 1;
  
}

.nav{
  display: flex;
  justify-content: center;
}
